<template>
	<transition name='mainBody'>
	<div class="content">
		<div class="card demo-card-header-pic">
			<div valign="bottom" class="card-header color-white no-border no-padding">
				<img class='card-cover' :src="$store.state.personalInfo[0].img" alt="">
			</div>
			<div class="card-content">
				<div class="card-content-inner">
					<div class="item-title">{{$store.state.personalInfo[0].name}}</div>
					<p class="color-gray">{{$store.state.personalInfo[0].time}}</p>
				</div>
			</div>
			<div class="card-footer">
				<span></span>
				<span class="open-preloader" @click='textInfo(0)'>查看详情>></span>
			</div>
		</div>
		<div class="list-block courseUl">
			<ul>
				<li class="item-content item-link" @click='textInfo(1)'>
					<div class="item-media"><i class="icon icon-f7"></i></div>
					<div class="item-inner">
					<div class="item-title">{{$store.state.personalInfo[1].name}}</div>
						<div class="item-after">{{$store.state.personalInfo[1].time}}</div>
					</div>
				</li>
				<li class="item-content item-link" @click='textInfo(2)'>
					<div class="item-media"><i class="icon icon-f7"></i></div>
					<div class="item-inner">
						<div class="item-title">{{$store.state.personalInfo[2].name}}</div>
						<div class="item-after">{{$store.state.personalInfo[2].time}}</div>
					</div>
				</li>
				<li class="item-content item-link" @click='textInfo(3)'>
					<div class="item-media"><i class="icon icon-f7"></i></div>
					<div class="item-inner">
						<div class="item-title">{{$store.state.personalInfo[3].name}}</div>
						<div class="item-after">{{$store.state.personalInfo[3].time}}</div>
					</div>
				</li>
				<li class="item-content item-link" @click='textInfo(4)'>
					<div class="item-media"><i class="icon icon-f7"></i></div>
					<div class="item-inner">
						<div class="item-title">{{$store.state.personalInfo[4].name}}</div>
						<div class="item-after">{{$store.state.personalInfo[4].time}}</div>
					</div>
				</li>
			</ul>
		</div>
	</div>
</transition>
</template>

<script>
	export default{
		methods:{
			textInfo(index){
				this.$store.state.dataLogic.textIndex=index;
				this.$store.dispatch('goTextInfo',index);
				this.$router.push({path:'/textPage'});
			}
		}
	}
</script>


