import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const router=new VueRouter({
	routes:[
	{
		path:'/',component:require('../view/homePage')
	},{
		path:'/introduction',component:require('../view/introduction')
	},{
		path:'/course',component:require('../view/course')
	},{
		path:'/textPage',component:require('../view/textPage')
	},{
		path:'/information',component:require('../view/information')
	},{
		path:'/itemPage',component:require('../view/itemPage')
	}]
})

export default router



