<template>
<transition name='mainBody'>
<div class="content">
    <div class="card demo-card-header-pic" v-for='(item,index) in $store.state.itemInfo'>
      <div valign="bottom" class="card-header color-white no-border no-padding">
        <img class='card-cover' :src="item.img" alt="">
      </div>
      <div class="card-content">
        <div class="card-content-inner">
          <p class="color-gray">{{item.desc}}</p>
          <p>{{item.title}}</p>
          <a :href="item.address" class="link">地址:{{item.address}}</a>
        </div>
      </div>
      <div class="card-footer">
        <span>{{item.nowState}}</span>
        <span @click='goItemInfo(index)'>查看详情>></span>
      </div>
    </div>
  </div>
</transition>
</template>

<script>
  export default{
    methods:{
      goItemInfo(index){
        this.$store.dispatch('getItemInfo',index);
        this.$router.push({path:'/itemPage'});
      }
    }
  }
</script>


