import Vue from 'vue'
import Vuex from 'vuex'
import VueResource from 'vue-resource'
import info from '../json/message'
import item from '../json/project'
Vue.use(Vuex)
Vue.use(VueResource)

const store=new Vuex.Store({
	state:{
		personalInfo:info,          //文章的数据
		itemInfo:item,				//项目的数据
		HideLogic:{
			hidetextPage:true
		},
		dataLogic:{
			textContent:[],				//文章的index数组，查询有没有点击过文章
			textData:'',					//存放的文章数据
			textIndex:'',				//文章的index
			itemData:'',					//项目的内容
			itemContent:[]
		}
	},
	mutations:{
		getTextData(state,index){
			state.dataLogic.textData=state.personalInfo[index];
		},
		getItemData(state,index){
			state.dataLogic.itemData=state.itemInfo[index];
		},
		runLoader(state,index){
			state.dataLogic.textContent.push(index);
			$.showPreloader('loader...');
			setTimeout(()=>{
        		$.hidePreloader();
        		state.dataLogic.textData=state.personalInfo[index];
    		}, 2000);
		},
		itemLoader(state,index){
			state.dataLogic.itemContent.push(index);
			$.showPreloader('loader...');
			setTimeout(()=>{
        		$.hidePreloader();
        		state.dataLogic.itemData=state.itemInfo[index];
    		},2000);
		}
	},
	actions:{
		getItemInfo({state,commit},index){
			state.dataLogic.itemData='';
			if(state.dataLogic.itemContent.length!=0){
				let b=1;
				for(let i=0;i<state.dataLogic.itemContent.length;i++){
					if(state.dataLogic.itemContent[i]==index){
						commit('getItemData',index);
						b=2;
						break;
					}
				}
				if(b!=2){
					commit('itemLoader',index);
				}
			}else{
				commit('itemLoader',index);
			}
		},
		goTextInfo({state,commit},index){
			state.dataLogic.textData='';
			if(state.dataLogic.textContent.length!=0){
				let b=1;
				for(let i=0;i<state.dataLogic.textContent.length;i++){
					if(state.dataLogic.textContent[i]==index){
						commit('getTextData',index);
						b=2;
						break;
					}
				}
				if(b!=2){
					commit('runLoader',index);
				}
			}else{
				commit('runLoader',index);
			}
		}
	}
})

export default store


