<template>
    <nav class="bar bar-tab back-opacity" id="footIcon">
      <a class="tab-item external active" href="javascript:void(0)" @click='goRouter(0)'>
        <span class="icon icon-home"></span>
        <span class="tab-label">主页</span>
      </a>
      <a class="tab-item external" href="javascript:void(0)" @click='goRouter(1)'>
        <span class="icon icon-edit"></span>
        <span class="tab-label">随笔</span>
        <span class="badge">5</span>
      </a>
      <a class="tab-item external" href="javascript:void(0)" @click='goRouter(2)'>
        <span class="icon icon-computer"></span>
        <span class="tab-label">项目</span>
      </a>
      <a class="tab-item external" href="javascript:void(0)" @click='goRouter(3)'>
        <span class="icon icon-card"></span>
        <span class="tab-label">资料</span>
      </a>
    </nav>
</template>

<script>
  export default{
    data(){
      return {
        footArr:''
      }
    },
    methods:{
      goRouter(index){
        if(this.footArr==''){
          this.getfootId();
        }
        for(var i=0;i<this.footArr.length;i++){
          $(this.footArr[i]).removeClass("active")
        }
        $(this.footArr[index]).addClass('active');
        switch(index){
          case 0:
          this.$router.push({path:'/'});
          break;
          case 1:
          this.$router.push({path:'/course'});
          break;
          case 2:
          this.$router.push({path:'/information'});
          break;
          case 3:
          this.$router.push({path:'/introduction'});
          break;
        }
      },
      getfootId(){
        this.footArr=$('#footIcon a');
      }
    }
  }
</script>

<style>
</style>



