<template>
	<transition name='mainBody'>
		<div class="content"  v-if='$store.state.HideLogic.hidetextPage'>
			<header class="bar bar-nav">
				<button class="button button-link button-nav pull-left" @click='backPage'>
					<span class="icon icon-left"></span>
					返回
				</button>
				<button class="button button-link button-nav pull-right"
				@click='nextText'>
				下一篇
				<span class="icon icon-right"></span>
			</button>
			<h1 class="title">{{$store.state.dataLogic.textData.name}}</h1>
		</header>
		<div class="textpageImg">
			<img :src="$store.state.dataLogic.textData.img">
		</div>
		<div class="content-padded">
			<p style="text-indent: 1.5rem">{{$store.state.dataLogic.textData.info}}</p>
		</div>
	</div>
</transition>
</template>

<script>
	export default{
		methods:{
			backPage(){
				this.$router.go(-1);
			},
			nextText(){
				var _this=this;
				this.$store.state.HideLogic.hidetextPage=false;
				this.$store.state.dataLogic.textIndex++;
				if(this.$store.state.dataLogic.textIndex==5){
					this.$store.state.dataLogic.textIndex=0;
				}
				this.$store.dispatch('goTextInfo',this.$store.state.dataLogic.textIndex);
				setTimeout(()=>{
					this.$store.state.HideLogic.hidetextPage=true;
				}, 200);
			}
		}
	}
</script>


