$(function(){
	document.addEventListener('touchmove', function(event) {
    	event.preventDefault();
	}, false);
	let obj={
		a:0,
		autoPlay:setInterval(autoMthods,4000),
		moveStartX:0,
		moveEndX:0,
		distance:0
	};
	$('#wrapperUl').bind("touchstart",(event)=>{
		var e=window.event||event
		obj.moveStartX=e.changedTouches[0].clientX
		clearInterval(obj.autoPlay);
	});
	$('#wrapperUl').bind("touchmove",()=>{
	});
	$('#wrapperUl').bind("touchend",(event)=>{
		var e = event || window.event;
		obj.moveEndX=e.changedTouches[0].clientX;
		obj.autoPlay=setInterval(autoMthods,4000);
		obj.distance=obj.moveEndX-obj.moveStartX;
		moveEle(obj.distance);
	});

	function moveEle(i){
		if(i>0){
			obj.a--;
			if(obj.a==-1){
				obj.a=2;
			}
		}else{
			obj.a++;
			if(obj.a==3){
				obj.a=0;
			}
		}
		moveWrap()
	}

	function autoMthods(){
		obj.a++;
		if(obj.a==3){
			obj.a=0;
		}
		moveWrap()
	}

	function moveWrap(){
		for(var i=0;i<3;i++){
			$('.bannerBtn i').eq(i).removeClass("active")
		}
		$('.bannerBtn i').eq(obj.a).addClass("active")
		$('.wrapperUl').css({
			'-webkit-transform':'translate('+(-25*obj.a)+'%)',
			'transform':'translate('+(-25*obj.a)+'%)',
			'transition':'400ms ease-in-out',
			'-webkit-transition':'400ms ease-in-out',
		})
	}
})



